	module.exports = {"data":{"markdownRemark":{"html":"<p>ここのところ空気が乾燥している所為か、静電気が凄い。 ドアに触るたびにバチッ、仕事中機械に触れるたびにバチッ、恋敵と目が合う度にバチバチッともうピカチュウ状態。最近ではすっかり恐怖症になってしまい、金属に触れるとき一瞬躊躇ってしまうくらいだ。 特に嫌なのが 駅の券売機でキップ買うとき。券売機で買うと硬貨がある場合は硬貨の先から放電されるので大丈夫だが、札しかない場合は買うときに覚悟しなくてはならない。こう言う時にSuicaって役立つなぁと思ってしまった。財布にしまったままなら直接手を触れることないので静電気が起きないし、レッツ&#x26;ゴー……ｹﾞﾌﾝｹﾞﾌﾝ、タッチ&#x26;ゴーは重宝するわけである。 しかしあと1ヶ月もすれば静電気の季節も終わりか……つーか唯でさえ寒いから早くリリーさん来てくれと'Ａ｀そして俺のベッドで（ｒｙ</p>","frontmatter":{"cover":null,"date":"2006-02-08T20:17:00+09:00","strDate":"2006/2/8","title":"静電気恐怖症"}}},"pathContext":{"path":"/ja/blog/2006/02/08-静電気恐怖症/","langKey":"ja"}}

